<template>
    <div>   
        <ul>
            <li v-for="item in dataList" class="mov clearfix">
                <div class="cover">
                    <img :src="'https://images.weserv.nl/?url=' + item.cover.url" alt="">
                </div> 
                <div class="text">
                    <p>{{item.title}}</p>
                    <p>{{item.actors}}</p>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    data() {
        return {
            dataList:[]
        }
    },
    created() {
        this.getData();
        let obj = {
            title:"电影",
            navName:"movie"
        }
        this.$emit("changeActive",obj)
    },
    methods: {
        getData(){
            let birdapi = 'https://bird.ioliu.cn/v2?url=';
            let urlapi = 'https://m.douban.com/rexxar/api/v2/subject_collection/movie_showing/items?start=${this.listStart}&count=10';
            axios.get(birdapi+urlapi)
            .then((res)=>{
                this.dataList=res.data.subject_collection_items   
                console.log(this.dataList);
                             
            })
        }
    },
    
}
</script>
<style scoped>
.mov{
    padding: 10px;
    border-bottom: 1px solid #ddd;
}
.cover{
    float: left;
    width: 40%;
}
.cover img{
    width: 90%;
}
.text{
    float: left;
    width: 60%;
    font-size: 0.3rem;
}
</style>